#snake {
  position: relative;
  margin: 10px;
  font-family: monospace;
}
#grid {
  transition: all ease 2s;
  display: grid;
  grid-template-columns: repeat(19, 1fr);
  grid-auto-rows: minmax(4vh, auto);
}

#start,
#startAgain {
  height: 50px;
  max-width: 250px;
  width: 100%;
  padding: 5px;
  margin-bottom: 10px;
  border-radius: 7px;
  border: none;
  font-size: 30px;
  text-align: center;
  outline: none;
  background-color: #253e30;
  color: #ffffff;
  transition: all ease 0.5s;
}

#start:hover, #startAgain:hover {
    cursor: pointer;
    background-color: #1f6f8c;
  }
/* position: absolute; */
.mobileButtonsContainer button {
    width: 5vw;
    position: absolute;
    height: 35vh;
    border: none;
    background: #1f6f8c;

    color: #fff;
  }
.mobileButtonsContainer button div {
      padding: 15px;
      background-image: url("./img/arrow.png");
      background-repeat: no-repeat;
      background-position: center;
    }
.mobileButtonsContainer .button_boost {
    width: 30vw;

    height: 4vh;
    left: 36%;
    bottom: -35px;
    z-index: 20;
  }
.mobileButtonsContainer .button_boost div {
      background-image: none;
      padding: 0;
    }
.arrow_down {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.arrow_left {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.arrow_right {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.modal {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.4);
}

.cell {
  background-image: url("./img/grass.png");
  background-position: bottom;
  background-size: cover;
  margin: -2px 0px;
}

.head,
.apple {
  background-color: #529d26;
  background-position: bottom;
  background-size: contain;
  background-repeat: no-repeat;
}

.head {
  background-image: url("./img/snake.png");
}
.move_rigth {
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
  }
.move_left {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
.move_down {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
.move_up {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }

.body {
  /* transition: all .1s ease; */

  background-image: url("./img/tail.png");
  background-position-x: 0px;
  background-position-y: -2px;
}

.apple {
  background-image: url("./img/apple.png");
}

@media (max-width: 1024px) {
  #grid {
    /* display:none; */
    grid-template-columns: repeat(19, 36px);
    /* grid-auto-rows: minmax(4vh, auto); */
    justify-content: center;
  }
}

@media (max-width: 768px) {
  #snake {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
  }
  #grid {
    /* display:none; */
    grid-template-columns: repeat(19, 33px);
    grid-auto-rows: minmax(3.7vh, auto);
    justify-content: center;
  }

  .button_up {
    top: 0;
    left: 10px;
  }
  .button_left {
    bottom: 0;
    left: 10px;
  }
  .button_down {
    top: 0;
    right: 11px;
  }
  .button_right {
    right: 11px;
    bottom: 0;
  }
}

@media (max-width: 425px) {
  #grid {
    /* display:none; */
    grid-template-columns: repeat(19, 20px);
    grid-auto-rows: minmax(2.1vh, auto);
    justify-content: center;
  }
    .mobileButtonsContainer button {
      width: 8vw;
      position: absolute;
      height: 26vh;
      border: none;
      background: #1f6f8c;
      color: #fff;
    }

  
  .button_up {
    top: 0;
    left: 0;
  }
  .button_left {
    bottom: 0;
    left: 0;
  }
  .button_down {
    top: 0;
    right: 0;
  }
  .button_right {
    right: 0;
    bottom: 0;
  }
}
