:root {
    --min-width: 1000px;
    --smallscreen: 480px;
}

#snake_app_container {
  display: grid;
  grid-gap: 50px;
  grid-template-columns: minmax(100px, 26vw) minmax(100px, 45vw);
  grid-auto-columns: minmax(0, 300px) minmax(0, 500px);
  grid-auto-rows: minmax(75.1vh, auto);
}

main {
  background-color: #253e30;
}

@media (max-width: 1024px) {
    #snake_app_container {
      /* display:none; */
        grid-gap: 5px;
        grid-template-columns: 1fr;
        grid-auto-rows: minmax(0, auto);
    }
}
